.products-slider {
  &__slider {
    margin-top: 25px;
    position: relative;
    @include screen(laptop) {
      margin-top: 50px;
    }
    .swiper {
      @include screen(laptop) {
        padding: 0 40px;
        margin: 0 -40px;
      }
      &-wrapper {
        align-items: stretch;
      }
      &-slide {
        height: auto;
        padding-top: 40px;
        border-radius: 42px;
      }
    }
    .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0;
    }
    .swiper-button-prev,
    .swiper-button-next {
      margin-top: 0;
      width: auto;
      transform: translateY(-50%);
      height: auto;
      display: none;
      @include screen(laptop) {
        display: block;
      }
      &:after,
      &:before {
        display: none;
      }
    }
  }
  &__more {
    background-color: $text;
    border-radius: 8px;
    padding: 11px 18px;
    display: inline-flex;
    align-items: center;
    gap: 10px;
    color: $white;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 7px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    line-height: 7px;
    margin-top: 25px;
    text-align: center;
    font-family: $gotham;
    @include screen(laptop) {
      line-height: 10px;
      border-radius: 12px;
      font-size: 11px;
      padding: 16px 26px;
      margin-top: 20px;
    }
  }
}