.card {
  height: 100%;
  border-radius: 24px;
  padding: 0 19px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @include screen(laptop) {
    border-radius: 42px;
  }
  &:hover {
    .card {
      &__image {
        &:before {
          width: 100%;
          height: 100%;
        }
      }
      &__favorites {
        opacity: 1;
      }
      &__tags {
        left: -4px;
      }
    }
  }
  &__content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__image {
    min-height: 140px;
    display: flex;
    align-items: flex-end;
    @include screen(laptop) {
      min-height: 210px;
    }
    &:before {
      content: '';
      left: 50%;
      transform: translateX(-50%);
      position: absolute;
      top: 0;
      width: 95%;
      transition: $transition-default;
      height: 200px;
      border-radius: 42px;
      background-color: #F3F2F9;
      @include screen(laptop) {
        width: 80%
      }
    }
    img {
      z-index: 2;
      position: relative;
      max-height: 212px;
      object-position: bottom;
      object-fit: contain;
      max-width: 140px;
      width: 100%;
      height: 100%;
    }
  }
  &__tags {
    position: absolute;
    left: 2px;
    top: 35px;
    z-index: 2;
    transition: $transition-default;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
    @include screen(laptop) {
      left: 31px;
    }
  }
  &__favorites {
    position: absolute;
    z-index: 2;
    top: 20px;
    right: 20px;
    opacity: 0;
    transition: opacity $transition-default;
    @include screen(laptop) {
      top: 35px;
      right: 35px;
    }
  }
  &__rating {
    display: flex;
    align-items: center;
    margin-top: 32px;
    gap: 4px;
  }
  &__description {
    font-size: 18px;
    margin-top: 15px;
    line-height: 27px;
    text-align: center;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;
    @include screen(laptop) {
      -webkit-line-clamp: 3;
    }
  }
  &__brand {
    font-size: 18px;
    line-height: 23px;
    text-align: center;
    margin-top: 15px;
    color: #545459;
  }
  &__price {
    margin-top: 15px;
    font-family: $gotham;
    font-weight: 500;
    font-size: 18px;
    font-weight: 700;
    line-height: 17px;
    text-align: center;
  }
  &__button {
    background-color: #F2F3F5;
    border-radius: 8px;
    padding: 11px 18px;
    display: inline-flex;
    align-items: center;
    gap: 8px;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 11px;
    line-height: 11px;
    margin-top: 20px;
    text-align: center;
    font-family: $gotham;
    span {
      margin-top: 4px;
    }
    svg {
      path {
        stroke: $text;
      }
    }
    @include screen(laptop) {
      background-color: $text;
      line-height: 10px;
      color: $white;
      border-radius: 12px;
      font-size: 11px;
      padding: 16px 26px;
      svg {
        path {
          stroke: $white;
        }
      }
    }
  }
}