.video-blog {
  &__items {
    display: flex;
    align-items: center;
    gap: 40px;
    margin-top: 40px;
    overflow-x: auto;
    @include screen(laptop) {
      justify-content: center;
      overflow-x: inherit;
    }
  }
  &__item {
    min-width: 96px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    img {
      padding: 5px;
      border-radius: 32px;
      width: 84px;
      height: 84px;
      object-fit: cover;
      border: 4px solid #B5ABD0;
    }
    span {
      display: inline-block;
      line-height: 21px;
    }
  }
}